@import "variables";

$bg-color: #df0174;

$size: 1em;

body {
    background-color: $bg-color;
    margin: $size * 2;
}



#main {
    width: 97%;
    p, div {
        font-size: 20px;
        a {
            text-decoration: none;
        }
    }
    span {
        font-size: 22px;
    }
}

a {
    font-weight: bold;
    &:hover {
        text-decoration: none;
    }
}

#main {
    color: blue;
    &-lateral {
        color: green;
    }
}
//comentario de una linea que no aparece en el css final

.parrafo {
    font: {
        family: fantasy;
        size: 20px;
        weight: bold;
    }
}

p {
    color: $color-texto;
}

@mixin tipo_enlace {
    color: blue;
    text-decoration: none;
}

#capa {
    a {
        @include tipo_enlace;
    }
}